<template>
    <div class="table">
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="采购单编号" :inline="true">
                    <el-input v-model.trim="filters.purchase_node" placeholder="采购单编号"></el-input>
                </el-form-item>
                <el-form-item label="供应商" :inline="true">
                    <el-input v-model.trim="filters.supplier" placeholder="供应商"></el-input>
                </el-form-item>
                <!-- <el-form-item label="订单阶段">
                    <el-select v-model="filters.value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="订单状态">
                    <el-select v-model="filters.value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item> -->
                <el-button>查询</el-button>
                <el-button type="primary" icon="plus" @click="$router.push({path: '/newCarManagement/edit', query:{addType: 2}})">新建</el-button>
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="purchase_node" label="采购单编号" width="150">
                </el-table-column>
                <el-table-column prop="sale_num_code" label="对应销售单号" width="120">
                </el-table-column>
                <el-table-column prop="total_car_num" label="车辆数" width="120">
                </el-table-column>
                <el-table-column prop="total_car_price" label="金额" width="120">
                </el-table-column>
                <el-table-column prop="supplier_name" label="供应商" width="120">
                </el-table-column>
                <el-table-column prop="stage_name" label="订单阶段" width="120">
                </el-table-column>
                <el-table-column prop="work_status_name" label="订单状态" width="120">
                </el-table-column>
                <el-table-column prop="user_name" label="当前处理人" width="120">
                </el-table-column>
                <el-table-column prop="create_time" label="创建日期" width="120">
                    <template slot-scope="{row}">
                        {{row.create_time | time}}
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button
                            type="text"
                            icon="el-icon-view"
                            @click="$router.push({path: '/newCarManagement/view', query: { id: row.pur_info_id}})">
                            查看
                        </el-button>
                        <el-button
                            v-if="isShowEdit(row)"
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/newCarManagement/edit', query: { id: row.pur_info_id}})">
                            修改
                        </el-button>
                        <el-button
                            v-if="isShowDispose(row)"
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/newCarManagement/edit', query: { id: row.pur_info_id, stage_id: row.stage_id}})">
                            处理
                        </el-button>
                        <el-button
                            v-if="row.auth_user==1&&row.work_status==2"
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/newCarManagement/edit', query: { id: row.pur_info_id}})">
                            审核
                        </el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(row, $index)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="limit">
                </el-pagination>
            </div>
        </div>

        <!-- 删除提示框 -->
        <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
            <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteRow">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import * as API from '@/api/salesManagement'
    import { requestUrl } from '@/api/config'
    import selectClient from '@/components/selectClient'
    export default {
        data() {
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                tableData: [],
                page: 1,
                total: 1,
                limit: 10,
                multipleSelection: [],
                loading: false,
                delVisible: false,
                dialogVisible: false,
                filters: {
                    supplier:'',
                    purchase_node:'',
                    value:'3'
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }]
            }
        },
        components:{
            selectClient
        },
        created() {
            this.getData();
        },
        computed: {
            
        },
        methods: {
            isShowDispose(row){
                if(row.stage_id!=1){
                    if(row.work_status==1 || row.work_status==3){
                        return row.deal_user==1
                    }
                }else{
                    if(row.node_id==37){
                        return true
                    }
                    return row.deal_user==1 && row.work_status==3
                }
                
            },
            isShowEdit(row){
                if(row.stage_id==14&&row.deal_user==1){
                    return true
                }
                return row.stage_id==1&&row.work_status==1&&row.deal_user==1
            },
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params= {
                    page: this.page,
                    ...this.filters
                }
                this.loading = true
                API.newCarManagementList(params).then(res => {
                    this.loading = false
                    if(res.code == 0){
                        this.tableData = res.data.rows;
                        this.total = res.total;
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleDelete(row, index) {
                this.$confirm('确认删除吗?').then(() => {
                    API.newCarManagementDel({pur_info_id: row.pur_info_id}).then(res => {
                        if(res.code == 0){
                            this.tableData.splice(index, 1);
                            this.$message.success('删除成功')
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            // 确定删除
            deleteRow(){
                this.tableData.splice(index, 1);
                this.tableData.splice(this.idx, 1);
                this.$message.success('删除成功');
                this.delVisible = false;
            }
        }
    }

</script>

<style scoped>
    
</style>
